import { ChangeEvent, useState } from "react";

type pageDataType = {
  id: string;
  question: string;
  answer: string;
}[];

export const FAQPage = () => {
  const [searchTerm, setSearchTerm] = useState<string>("");
  const [expandedIds, setExpandedIds] = useState<string[]>([]);
  const pageData: pageDataType = [
    {
      id: "1",
      question: "What is React?",
      answer: "React is a JavaScript library for building user interfaces.",
    },
    {
      id: "2",
      question: "How does state work in React?",
      answer:
        "State is an object that holds the data that may change over time.",
    },
  ];

  const handleSearch = (e: ChangeEvent<HTMLInputElement>) => {
    setSearchTerm(e.target.value);
  };

  const toggleAnswer = (id: string) => {
    setExpandedIds((prevExpandedIds) =>
      prevExpandedIds.includes(id)
        ? prevExpandedIds.filter((expandedId) => expandedId !== id)
        : [...prevExpandedIds, id]
    );
  };

  const filteredData = pageData.filter(
    (item) =>
      item.question.toLowerCase().includes(searchTerm.toLowerCase()) ||
      item.answer.toLowerCase().includes(searchTerm.toLowerCase())
  );

  return (
    <div>
      <input
        type="text"
        placeholder="Search..."
        value={searchTerm}
        onChange={handleSearch}
      />
      {filteredData.length > 0 ? (
        <ul>
          {filteredData.map((item) => (
            <li key={item.id}>
              <button onClick={() => toggleAnswer(item.id)}>
                {expandedIds.includes(item.id) ? "Hide Answer" : "Show Answer"}
              </button>
              <strong>{item.question}</strong>
              {expandedIds.includes(item.id) && <p>{item.answer}</p>}
            </li>
          ))}
        </ul>
      ) : (
        <p>Sorry, No results found.</p>
      )}
    </div>
  );
};
