export const NewsPage = () => {
  const { t } = useTranslation();
  const paigeData = getData("News", t) as NewsPageDataType;
  const { rating } = paigeData;
  const { id } = useParams();
  const navigate = useNavigate();

  useEffect(() => {
    console.log(`добавить просмотр новости с ${id}`);
  }, [id]);

  const selectedNews = newsData.find((news) => news.id === id);

  if (!selectedNews) {
    return <NotFoundPage />;
  }

  const remainingNews = newsData.filter((news) => news.id !== id);

  const onClickHandler = (id: string) => {
    navigate(`/news/${id}`);
  };

  const wordCount = selectedNews.text.split(" ").length;
  const averageReadingSpeed = 250; // Средняя скорость чтения в словах в минуту
  const estimatedReadingTime = Math.ceil(wordCount / averageReadingSpeed);

  return (
    <NewsContainer>
      <SelectedNews>
        <StyledImage
          size="big"
          src={selectedNews.url}
          alt={selectedNews.title}
        />
        <StyledSelectedContent>
          <StyledDate>{selectedNews.date}</StyledDate>
          <StyledSelectedContainer>
            <StyledSubtitle size="big">{selectedNews.title}</StyledSubtitle>
            <StyledSelectedInfo>
              <Rating
                id={selectedNews.id}
                initialValue={selectedNews.rating}
                ratingData={rating}
              />
              <StyledSelectedWrapper>
                <StyledSelectedImage
                  src={paigeData.views.src}
                  alt={paigeData.views.alt}
                />
                <StyledSelectedSpan>{selectedNews.views}</StyledSelectedSpan>
              </StyledSelectedWrapper>
              <StyledSelectedWrapper>
                <StyledSelectedImage
                  src={paigeData.time.src}
                  alt={paigeData.time.alt}
                />
                <StyledSelectedSpan>
                  {estimatedReadingTime} {paigeData.time.text}
                </StyledSelectedSpan>
              </StyledSelectedWrapper>
            </StyledSelectedInfo>
            <StyledText>{selectedNews.text}</StyledText>
          </StyledSelectedContainer>
        </StyledSelectedContent>
      </SelectedNews>
      <RemainingNews>
        <Share selectedNews={selectedNews} />
        {remainingNews.map((news) => (
          <NewsPreview key={news.id} onClick={() => onClickHandler(news.id)}>
            <StyledImage size="small" src={news.url} alt={news.title} />
            <StyledWrapper size="small">
              <StyledSpan>{news.span}</StyledSpan>
              <StyledDate>{news.date}</StyledDate>
            </StyledWrapper>
            <StyledSubtitle size="small">{news.title}</StyledSubtitle>
          </NewsPreview>
        ))}
      </RemainingNews>
    </NewsContainer>
  );
};
