import React from "react";
import styled from "styled-components";

const ShareButtonsContainer = styled.div`
  display: flex;
  gap: 10px;
`;

const ShareButton = styled.button`
  padding: 10px 20px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
`;

const TelegramButton = ({ selectedNews }) => {
  const shareOnTelegram = () => {
    const message = encodeURIComponent(
      selectedNews.title + "\n" + selectedNews.text
    );
    const link = `https://telegram.me/share/url?url=&text=${message}`;
    window.open(link, "_blank");
  };

  return <ShareButton onClick={shareOnTelegram}>Share on Telegram</ShareButton>;
};

const FacebookButton = () => {
  const shareOnFacebook = () => {
    const url = encodeURIComponent(window.location.href);
    const link = `https://www.facebook.com/sharer/sharer.php?u=${url}`;
    window.open(link, "_blank");
  };

  return <ShareButton onClick={shareOnFacebook}>Share on Facebook</ShareButton>;
};

const EmailButton = () => {
  const shareByEmail = () => {
    const subject = encodeURIComponent(selectedNews.title);
    const body = encodeURIComponent(
      selectedNews.title + "\n" + selectedNews.text
    );
    const link = `mailto:?subject=${subject}&body=${body}`;
    window.location.href = link;
  };

  return <ShareButton onClick={shareByEmail}>Send via Email</ShareButton>;
};

export const Share = () => {
  return (
    <ShareButtonsContainer>
      <TelegramButton />
      <FacebookButton />
      <EmailButton />
    </ShareButtonsContainer>
  );
};

